  color: white;
  display: flex;
  flex-direction: row;
  font-family: Arial, Helvetica, sans-serif;
  height: 15vh;
  justify-content: space-around;
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: 6; }

a {
  color: white;
  text-decoration: none; }

button {
  background-color: black;
  border-radius: 10px;
  border: 2px solid white;
  color: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  outline: none;
  padding: 5px 15px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s; }
  button:hover {
    background-color: white;
    border-color: black;
    color: black;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s; }
  button:active {
    background-color: #00045e;
    border-color: white;
    color: white;
    -webkit-transition-duration: 0s;
            transition-duration: 0s; }

.logo {
  font-family: 'Bungee Inline', cursive;
  font-size: 50px;
  text-shadow: 3px 3px black;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s; }
  .logo:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s; }
  .logo:active {
    color: #00045e;
    -webkit-transition-duration: 0s;
            transition-duration: 0s; }

h4 {
  left: 10px;
  position: absolute;
  top: -13px; }

.nav-btn {
  -webkit-transform: scale(0.7);
          transform: scale(0.7); }
  .nav-btn:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9); }

@media only screen and (max-width: 1040px) {
  .logo {
    font-size: 2em; }
  button {
    font-size: 0.7em; } }

.card-text {
  opacity: 0;
  position: absolute;
  z-index: 4; }

.movie-card {
  border-radius: 2px;
  box-shadow: 2px 2px 8px rgba(255, 255, 255, 0.6);
  color: white;
  font-family: "Acme", sans-serif;
  height: 450px;
  margin: 20px;
  position: relative;
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  width: 300px; }
  .movie-card:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    box-shadow: 3px 3px 8px red; }
    .movie-card:hover img {
      -webkit-filter: blur(5px);
              filter: blur(5px); }
    .movie-card:hover .card-text {
      align-items: center;
      display: flex;
      flex-direction: column;
      font-size: 16px;
      height: 95%;
      justify-content: space-between;
      margin: 16px;
      opacity: 1;
      overflow: scroll;
      padding: 3px;
      position: absolute;
      text-align: center;
      text-shadow: 2px 2px black;
      -webkit-transition-delay: .2s;
              transition-delay: .2s;
      -webkit-transition: opacity .4s linear;
      transition: opacity .4s linear;
      width: 80%; }
      .movie-card:hover .card-text::-webkit-scrollbar {
        display: none; }

.movie-title {
  font-size: 34px;
  -webkit-text-stroke-width: .1px;
  -webkit-text-stroke-color: black; }

.poster img {
